<template>
  <el-container class="casilla" style="height: calc(100% - 75.5px); background:#fff ;border: 1px solid #eee">
    <el-aside width="245px" style="background-color: rgb(238, 241, 246)">
      <h5>Carpetas de Correo</h5>
      <el-menu
        default-active="1"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
      >
        <el-menu-item @click="cambiarBandeja(1)" index="1">
          <i class="el-icon-message"></i>
          <span >Bandeja de entrada ({{cantidadCasilla}})</span>
        </el-menu-item>
        <el-menu-item @click="cambiarBandeja(2)" index="2">
          <i class="el-icon-message"></i>
          <span>No leidos ({{cantidadNoLeidos}})</span>
        </el-menu-item>
      </el-menu>
    </el-aside>

    <el-container>
      <!-- <el-header style="text-align: right; font-size: 12px">
      <el-dropdown>
        <i class="el-icon-bell" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>Tiene nuevo mensaje</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <span>Notificaciones</span>
      </el-header>-->

      <el-main>
        <el-table :data="tableData" height="250">
          <!-- <el-table-column prop="subject" label="Mensajes">
          </el-table-column>-->
          <el-table-column label="Mensajes">
            <template slot-scope="scope">
              <i class="el-icon-message" style="margin-right: 10px"></i>
              <a href="javascript:;" @click="verDetalle(scope.row.subject,scope.row.read,scope.row.idCasilla,scope.row.textoNotificacion,scope.row.listaDocumentos)"> <span :class="{ 'active': !scope.row.read }">{{ scope.row.subject}}</span> </a>
              <p>{{ scope.row.dateTime }}</p>
            </template>
          </el-table-column>
        </el-table>
        <div style="height: calc(95% - 343.5px); margin: 20px;">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>{{detalleCasilla.asunto}}</span> <a v-if="detalleCasilla.idCasilla>0" href="javascript:;" @click="verAdjuntos(detalleCasilla.idCasilla)"><el-button style="float: right; padding: 3px 0" type="text"> Ver Documentos</el-button></a>   
              <!-- <a v-if="detalleCasilla.idCasilla>0" href="javascript:;" @click="verAdjuntos(detalleCasilla.idCasilla)"> <button class="btn btn-default"> Ver Adjuntos</button></a> -->
              <!-- <el-button style="float: right; padding: 3px 0" type="text">Operation button</el-button> -->
            </div>
            <div v-html="detalleCasilla.textoNotificacion" class="text item"></div>
          </el-card>
        </div>
        <!-- <div style="height: calc(76% - 343.5px); margin: 20px;">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span class="change"> <a :href="detalleCasilla.asunto" target="_blank">
                          <img src="../../assets/pdf.png" alt />
                          <p class="nom">{{detalleCasilla.asunto}}</p>
                        </a> 
              </span>
              <span class="change"> <a :href="detalleCasilla.asunto" target="_blank">
                          <img src="../../assets/pdf.png" alt />
                          <p class="nom">{{detalleCasilla.asunto}}</p>
                        </a> 
              </span>
              <span class="change"> <a :href="detalleCasilla.asunto" target="_blank">
                          <img src="../../assets/pdf.png" alt />
                          <p class="nom">{{detalleCasilla.asunto}}</p>
                        </a> 
              </span>
            </div>
          </el-card>
        </div> -->
    <el-dialog class="no-shadow" title="Vizualizar Archivos" :visible.sync="modalDocumentos" >
          <el-card v-for="doc of detalleCasilla.listaDocumentos" :key="doc.idDocumentoCasilla" class="box-card" shadow="hover">
            <div class="row align-center">
              <div class="col-md-4">
                <div class="text item title">{{doc.nombreTipoAdjunto}}</div>
              </div>
              <div class="col-md-4">
                <div class="text item">{{doc.nombreArchivo}}</div>
              </div>
              <div class="col-md-4">
                <div class="text item"> <a :href="doc.url" @click="actualizaAdjuntoLeido(doc.idDocumentoCasilla)" target="_blank"><el-button icon="el-icon-view">Visualizar Archivo</el-button></a> </div>
              </div>
            </div>
          </el-card>
	  </el-dialog>    
    <b-modal id="modalAdjuntos" hide-footer title="Adjuntos" @close="clearData">
      <div class="form-group" style="margin-bottom: 0.5rem; !important">
        <div v-for="doc of detalleCasilla.listaDocumentos" :key="doc.idDocumentoCasilla" class="row mt-2">
          <div class="col-md-12"> 
              <el-card class="box-card" shadow="hover">
            <div class="row align-center">
              <div class="col-md-4">
                <div class="text item title">{{doc.nombreTipoAdjunto}}</div>
              </div>
              <div class="col-md-4">
                <div class="text item">{{doc.nombreDocumento}}</div>
              </div>
              <div class="col-md-4">
                <div class="text item"> <a :href="doc.url" @click="actualizaAdjuntoLeido(doc.idDocumentoCasilla)"><el-button icon="el-icon-view">Visualizar Archivo</el-button></a> </div>
              </div>
            </div>
            </el-card>
          </div>

          <div class="col-md-4">
            <span v-for="doc of detalleCasilla.listaDocumentos" :key="doc.idDocumentoCasilla" class="change"> 
              <a :href="doc.url" @click="actualizaAdjuntoLeido(doc.idDocumentoCasilla)" target="_blank">
                <img src="../../assets/pdf.png" alt />
                <p class="nom">{{doc.nombreArchivo}}</p>
              </a> 
            </span>
              
          </div>
        </div>
      </div>
    </b-modal>
      </el-main>
    </el-container>
  </el-container>
  
</template>

<style lang="scss">
.row.align-center {
    align-items: center;
    .text.item.title {
      color: #0078cf;
      font-weight: 400;
    }
  }
  .no-shadow {
    .el-dialog__body {
      padding: 10px 20px;
    }
    .el-card__body {
      padding: 4px 10px;
    }
  }
.modal-backdrop {
    background: #002646a8;
  }
  .modal-content {
    border: 0px;
    border-radius: 20px;
    border: solid 1px #e0e0e0;
    // box-shadow: 0px 0px 12px 3px rgba(0, 125, 197, 0.19);
  }
  .modal-body,
  .modal-header {
    box-shadow: none;
  }
  span.change {
            display: inline-block;
            position: relative;
            padding-left: 42px;
            p.nom {
              white-space: nowrap;
              //overflow: hidden;
              //text-overflow: ellipsis;
              margin-bottom: 0px;
              max-width: 100px;
            }
            img {
              position: absolute;
              left: 4px;
              top: 0px;
            }
          }
.casilla {
  span.change {
            display: inline-block;
            position: relative;
            padding-left: 42px;
            p.nom {
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              margin-bottom: 0px;
              max-width: 100px;
            }
            img {
              position: absolute;
              left: 4px;
              top: 0px;
            }
          }
.text {
  font-size: 14px;
}
.el-table td p{
    margin-bottom: 0;
}
.item {
  margin-bottom: 18px;
  text-align: left;
  margin: inherit;
  max-width: 100%;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  height: 100%;
}

.el-main {
  padding: 0;
}

.el-header {
  background-color: #0078cf;
  color: #fff;
  line-height: 60px;
  i {
    color: #fff;
  }
}
.active {
  color: #000;
  font-weight: bold;
}

.el-aside {
  h5 {
    color: #0078cf;
    line-height: 48px;
    text-align: center;
    margin-bottom: 0;
  }
}

}
</style>

<script>
import Constantes from '../../store/constantes'
import axios from "axios";
export default {
  data() {
    return {
      tableData: [],
      tableDataGeneral:[],
      tableDataNoLeido:[],
      listaCasilla: [],
      cantidadCasilla:0,
      cantidadNoLeidos:0,
      asunto:'asunto inicial',
      textoNotificacion:'texto inicial',
      detalleCasilla:{
        asunto:'',
        textoNotificacion:'',
        listaDocumentos:[],
        idCasilla:0
      },
      modalDocumentos: false,
    };
  },
  mounted(){
   this.cargaCasilla(localStorage.getItem("idUsuarioLogueadoTramite"),1);
   
  },  
  methods: {
    openAdjuntos() {
      this.$bvModal.show("modalAdjuntos");
    },
    verAdjuntos(idCasilla){
      this.modalDocumentos=true
      //this.openAdjuntos();
    },
    clearData(){
    },
    actualizaAdjuntoLeido(idDocCasilla){
      axios.post(Constantes.rutaTramite+'actualizacasillaadjunto/'+idDocCasilla)
                    .then(response=>{
                          if(response.data.success){
                            console.log('documento leido' + idDocCasilla)
                          }
                          
                  })
                  .catch(e=>console.log(e))
    },
    verDetalle(asunto,flagLeido,idCasilla,textoNotificacion,listaDocumentos){
      console.log('ver detalle======>')
      console.log('idCasilla======>'+idCasilla)
      console.log('ver detalle======>')
      this.detalleCasilla.asunto=asunto
      this.detalleCasilla.textoNotificacion=textoNotificacion
      this.detalleCasilla.listaDocumentos=[]
      this.detalleCasilla.idCasilla=idCasilla
      listaDocumentos.forEach(element => {
        let documento={}
        documento.url=Constantes.filesRecuperaPrincipal + element.idTipoEntidad + '/'
                      +element.idEntidad
        documento.nombreArchivo=element.nombreDocumento
        documento.idDocumentoCasilla=element.idDocumentoCasilla                  
        documento.nombreTipoAdjunto=element.nombreTipoAdjunto
        this.detalleCasilla.listaDocumentos.push(documento)
      });
      
      if(!flagLeido)
      {
        this.actualizarFlagLeido(idCasilla);
      }
    },
    actualizarFlagLeido(idCasilla){
      axios.post(Constantes.rutaTramite+'actualizacasilla/'+idCasilla+'/1')
                    .then(response=>{
                          if(response.data.success){
                            this.cantidadNoLeidos=this.cantidadNoLeidos-1
                            this.tableDataGeneral.forEach(element => {
                              if(element.idCasilla==idCasilla){
                                element.read=true
                              }
                            });
                          }
                          
                  })
                  .catch(e=>console.log(e))
    },
    cambiarBandeja(tipo){
      this.cargaCasilla(999,tipo);
      
    }, 
    cargaCasilla(idUsuario,tipo){
      axios.get(Constantes.rutaTramite+'casillaelectronica/'+localStorage.getItem("idUsuarioLogueadoTramite"))
      //axios.get(Constantes.rutaTramite+'casillaelectronica/'+999)
                    .then(response=>{
                          this.tableData=[]
                          this.tableDataGeneral=[]
                          this.tableDataNoLeido=[]
                          this.cantidadCasilla=0
                          this.cantidadNoLeidos=0
                          response.data.data.forEach(element => {
                            let casilla={}
                            casilla.subject=element.asuntoCasilla
                            casilla.dateTime=element.fecRecepcion
                            casilla.read=element.indLectura==1?true:false
                            casilla.idCasilla=element.idCasillaElectronica
                            casilla.textoNotificacion=element.textoNotificacion
                            casilla.listaDocumentos=element.listaDocumentos
                            this.cantidadCasilla+=1
                            if(element.indLectura==0){
                              this.tableDataNoLeido.push(casilla)
                              this.cantidadNoLeidos+=1
                            }
                            this.tableDataGeneral.push(casilla)
                          });
                          this.tableData=tipo==1?this.tableDataGeneral:this.tableDataNoLeido

                  })
                  .catch(e=>console.log(e))
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>


